*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}
.App{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #913175;
    overflow: hidden;
    color: #20262E;
}

.App .container i{
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: #E0A4CE;
}
.App .container h1{
    margin-top: 0;
    margin-bottom: 2%;
}
.App .container h3{
    margin-top: 2%;
    margin-bottom: 0%;
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 3px solid rgb(0, 0, 0);
    height: 80vh;
    padding: 20px; 
    color: #EDDBC7;
    font-size: 20px;
    border-radius: 10px;
}
.todoform{
    display: flex;
    width: 420px;
    justify-content: space-around;
    
}

input{
    width: 80%;
    border-radius: 20px;
    padding: 10px;
    
}

button{
    border-radius: 20px;
    border: none;
    padding: 10px;
    margin: 0 5px;
}

.list{
 list-style: none;
 margin: 0;
 padding: 0;
}
.items{
display: flex;
margin: 20px 0px;
background-color: #CD5888;
padding: 10px 20px;
border-radius: 50px;
width: 450px;
align-items: center;
}
.text{
    flex: 1;
}